@import url(https://fonts.googleapis.com/css2?family=Goldman&family=Roboto:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  padding: 0;
  margin: 0;
  outline: 0;
  box-sizing: border-box;
}

body {
  font-family: "Source Sans Pro", Helvetica, Arial, Sans-Serif;
}

div.title {
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin-top: 2%;

  margin: 0;
  background-color: rgb(95, 90, 90);
  height: 70px;
}

div.title h1 {
  color: white;
}

div.title ul {
  display: flex;
}

div.title ul li {
  list-style: none;
  padding: 5px;
}
div.title ul li a {
  color: white;
}

div.title ul li a.cadastro {
  text-decoration: none;
  font-weight: 700;
  font-size: 15px;
  color: rgb(238, 238, 97);
  border: 1px solid yellow;
  border-radius: 5px;

  padding: 5px;
}

h1 {
  color: rgb(248, 248, 69);
}

a {
  color: rgb(248, 248, 69);
}

.align {
  display: flex;
  flex-direction: row;
  align-items: center;
  padding-left: 7%;
  margin-top: 5%;
}

.first-news {
  text-align: center;
  padding-top: 5%;
  width: 700px;
  color: white;

  border-radius: 5px;
  box-shadow: 0 0 8px white;
  padding: 20px;
  margin-bottom: 20px;
  padding-left: -150px;
}

.media-lg {
  height: 200px;
  width: 300px;
}

.second-news {
  padding-left: 2%;
}

.sub-news {
  display: flex;

  text-align: center;
  padding-top: 5%;
  height: 200px;
  width: 500px;
  color: white;

  border-radius: 5px;
  box-shadow: 0 0 8px white;
  padding: 20px;
  margin-bottom: 20px;
}

.sub-news a {
  padding-left: 10px;
  padding-top: 5%;
}

.sub-news .second-img {
  padding-top: 10px;
}

.sub-news .media-sm {
  height: 100px;
  width: 200px;
}

.footer-news {
  display: flex;
  align-items: center;
  margin-left: 18%;
}

.footer-subnews {
  display: flex;
  flex-direction: column;
  align-items: center;

  padding-left: 10%;

  padding-top: 5%;
  width: 400px;
  color: white;

  margin-left: 35px;

  border-radius: 5px;
  box-shadow: 0 0 8px white;
  padding: 20px;
  margin-bottom: 20px;
}

.footer-news-img {
  height: 200px;
  width: 250px;
}

.footer-subnews a {
  font-size: 24px;
  padding-left: 15px;
}

* {
  padding: 0;
  margin: 0;
  outline: 0;
  box-sizing: border-box;
}

.acoes {
  text-align: center;
  padding-top: 5%;

  width: 100px;
  color: white;

  border-radius: 5px;
  box-shadow: 0 0 8px white;
  padding: 20px;
  margin-top: 90px;
  margin-bottom: 20px;
}

.align-acoes {
  display: flex;
  flex-direction: row;

  align-items: center;
  justify-content: space-around;
}

p.symbol {
  color: yellow;
}

p.change-percent {
  color: green;
}

.grafico {
  width: 800px;
  height: 400px;
  margin-left: 22%;
  margin-top: 5%;
  margin-bottom: 5%;
}

.footer {
  display: flex;
  align-items: center;
  justify-content: space-around;

  height: 50px;
  background-color: rgb(95, 90, 90);
}


body {
  background-color: rgb(20, 19, 19);
}

